import React from "react";
import { RiSettings2Line } from "react-icons/ri";
import { Link } from "gatsby";

export default ({ code, name, icon, createdAt }) => {
    createdAt = new Date(createdAt);
    let created = createdAt.toDateString() === new Date().toDateString() ? createdAt.toLocaleTimeString() : createdAt.toDateString();

    return (
        <Link to={ "/" + code }>
            <div css={{
                padding: 15,
                display: "flex",
                flexDirection: "column",
                backgroundColor: "rgba(100, 100, 100, .2)",
                transition: "background-color var(--transition-default-time) ease-out",
                borderRadius: 3,
                ":hover": {
                    backgroundColor: "rgba(100, 100, 100, .3)",
                },
            }}>
                <div css={{
                    display: "flex",
                    alignItems: "center",
                    marginBottom: 15,
                }}>
                    {
                        icon
                        ?   <img
                                alt=""
                                src={ icon }
                                height={ 64 }
                                width={ 64 }
                                css={{
                                    marginRight: 15,
                                }}
                            />
                        :   null
                    }
                    <div css={{
                        fontSize: ".9em",
                    }}>
                        { name }
                    </div>
                </div>
                <div css={{
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                }}>
                    <div css={{
                        fontSize: ".7em",
                        color: "grey",
                    }}>
                        { created }
                    </div>
                    <div css={{
                        display: "flex",
                        justifyContent: "flex-end",
                        "svg": {
                            color: "grey",
                            transition: "color var(--transition-default-time) ease-out",
                        },
                        ":hover svg:hover": {
                            color: "white",
                        },
                    }}>
                        <Link to={ "/" + code + "/settings" }>
                            <RiSettings2Line />
                        </Link>
                    </div>
                </div>
            </div>
        </Link>
    );
};
