import React from "react";
import { connect } from "react-redux";
import { navigate } from "gatsby";

import Helmet from "../components/Helmet";
import ConfirmDialog from "../components/Dialog/ConfirmDialog";
import Loader from "../components/Loader";
import Card from "../components/Org/Card";
import NewCard from "../components/Org/NewCard";
import isBrowser from "../utils/isBrowser";

const AccountPage = ({ me }) => {
    const [ processing, setProcessing ] = React.useState(false);
    const [ success, setSuccess ] = React.useState(undefined);
    const [ confirmMessage, setConfirmMessage ] = React.useState("");

    const [ orgs, setOrgs ] = React.useState([]);

    const deleteAccount = () => {
        if (processing) return;
        if (orgs.length) return;

        setProcessing(true);
        setConfirmMessage("");

        if (!isBrowser()) return;

        window.fetch(process.env.API_URL + "/login", {
            method: "DELETE",
            headers: {
                "Accept": "application/json",
                "Authorization": me.token,
            },
        }).then(res => {
            if (res.ok) return res.json();
            throw new Error(res.statusText);
        }).then(() => {
            setProcessing(false);
            navigate("/sayonara");
        }).catch((e) => {
            console.error(e);
            setProcessing(false);
        });
    };

    React.useEffect(() => {
        if (!isBrowser()) return;

        window.fetch(process.env.API_URL + "/orgs", {
            headers: {
                Accept: "application/json",
                Authorization: me.token,
            },
        }).then(res => {
            if (res.ok) return res.json();
            throw new Error(res.statusText);
        }).then(orgs => {
            setSuccess(true);
            setOrgs(orgs);
        }).catch((e) => {
            console.error(e);
            setSuccess(false);
        });
    }, [ me.token ]);

    return (
        <>
            <Helmet
                title="Account"
                description="Manage your Glee account and organizations."
            />
            <div>
                <div css={{
                    marginBottom: 5,
                    fontSize: "1.2em",
                    fontWeight: 500,
                }}>
                    Profile
                </div>
                <div css={{
                    marginBottom: 25,
                    color: "lightgrey",
                }}>
                    <div>Your profile data is synced with your GitHub account.</div>
                    <div>Update them in GitHub and you'll see the changes next time you login.</div>
                </div>
                <div css={{
                    display: "flex",
                    flexWrap: "wrap",
                    alignItems: "center",
                }}>
                    <img
                        alt="Avatar"
                        src={ "https://avatars2.githubusercontent.com/u/" + me.id + "?size=256" }
                        width={ 128 }
                        height={ 128 }
                        css={{
                            borderRadius: 3,
                            marginRight: 15,
                        }}
                    />
                    <div>
                        <div>
                            <div css={{
                                color: "darkgrey",
                                fontSize: ".8em",
                                fontWeight: 500,
                                textTransform: "uppercase",
                            }}>
                                User
                            </div>
                            <div>{ me.name || me.login }</div>
                        </div>
                        <div css={{ marginTop: 10 }}>
                            <div css={{
                                color: "darkgrey",
                                fontSize: ".8em",
                                fontWeight: 500,
                                textTransform: "uppercase",
                            }}>
                                Email
                            </div>
                            <div>{ me.email }</div>
                        </div>
                    </div>
                </div>
            </div>

            <div css={{
                marginTop: 50,
            }}>
                <div css={{
                    marginBottom: 5,
                    fontSize: "1.2em",
                    fontWeight: 500,
                }}>
                    Organizations
                </div>
                <div css={{
                    marginBottom: 25,
                    color: "lightgrey",
                }}>
                    You are a member of these organizations.
                </div>
                <div css={{
                    display: "grid",
                    gap: 15,
                    gridRowGap: 15,
                    gridTemplateColumns: `repeat(${ success ? "auto-fill" : "auto-fit" }, minmax(250px, 1fr))`,
                }}>
                    {
                        success === true
                        ?   <>
                                {
                                    orgs.map(org =>
                                        <Card
                                            key={ org.code }
                                            code={ org.code }
                                            name={ org.name }
                                            icon={ org.icon }
                                            createdAt={ org.createdAt }
                                        />
                                    )
                                }
                                <NewCard />
                            </>
                        :   success === false
                            ?   <div css={{ fontSize: ".8em", color: "darkgrey" }}>Oh, crap! Our servers having some issues. Please try again.</div>
                            :   <Loader />
                    }
                </div>
            </div>

            <div css={{
                marginTop: 50,
                padding: 15,
                border: "2px solid var(--red)",
                borderRadius: 3,
                backgroundColor: "rgba(255, 69, 58, .1)",
            }}>
                <div css={{ fontSize: ".9em", marginBottom: 5 }}>Danger Zone</div>
                <div css={{ color: "lightgrey", fontSize: ".8em", marginBottom: 15 }}>
                    When you delete your account, we'll remove the connection to your GitHub account, And then anonymize this account so that this account cannot be linked back to you. This way we preserve the votes, comments, or posts made by you in an organization without having to store your data. This cannot be undone.
                </div>
                <div css={{
                    width: "100%",
                }}>
                    <button
                        disabled={ processing || orgs.length }
                        css={{
                            fontSize: ".8em",
                            backgroundColor: "var(--red)",
                            color: "var(--light)",
                            ":disabled": {
                                backgroundColor: "darkred",
                                color: "lightgrey",
                                cursor: "not-allowed",
                            },
                        }}
                        onClick={ () => setConfirmMessage("Are you sure you want to delete your account? This cannot be undone.") }
                    >
                        {
                            processing ? "Deleting" : "Delete Account"
                        }
                    </button>
                    {
                        orgs.length
                        ?   <div css={{
                                marginTop: 10,
                                fontSize: ".8em",
                                color: "var(--red)",
                            }}>
                                You cannot delete your account as long as you're the owner of any organizations.
                            </div>
                        :   null
                    }
                </div>
            </div>

            {
                confirmMessage
                ?   <ConfirmDialog
                        title={ "Delete Account" }
                        message={ confirmMessage }
                        okHandler={ deleteAccount }
                        cancelHandler={ () => setConfirmMessage("") }
                    />
                :   null
            }
        </>
    );
}

export default connect(
    state => ({ me: state.global.me }),
)(AccountPage);
